@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex: 1 1;
  flex-direction: row;
 
}
.main {
    display: flex;
    flex: 1 1;
    height : 100vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.scrollerContainer{
    display: flex;
    flex: 1 1;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: url(/static/media/bg2.54566fca.jpg) center;
    background-size: cover;
    outline: none;
}

.navbar {
    width: 20%;
    height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: #1b0b38;
}
@font-face {
    font-family: 'Neoneon';
    font-style: normal;
    src: url(/static/media/Neoneon.696ade59.otf);
}

div.container
{
    font-family: 'Neoneon';
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border-radius: 3ch;
}

div.container a
{
    text-decoration: none;
    font: 30px "Neoneon";
    margin: 0px 10px;
    padding: 10px 10px;
    position: relative;
    z-index: 0;
    cursor: pointer;
    color: white;
}

div.highlightTextOut a:before, div.highlightTextIn a:before
{
    position: absolute;
    color: rgb(236, 106, 219);
    overflow: hidden;
    content: attr(alt);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
} 

div.highlightTextOut a:hover:before, div.highlightTextIn a:hover:before
{
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.img{
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    width: 30vh;
}

.img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.icon{
    margin: 1vh
}

.icon:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.component {
    display: flex;
    flex: 1 1;
    background-color: #1b0b38;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 2ch;
}
.first-component{
    background-color: transparent;
}

h2{
    color: white;
}
